.App {
  margin-top: 30px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.image {
  height: 40vmin;
}

.bet {
  display: flex;
  min-width: 250px;
  justify-content: space-between;
  align-items: center;
}

.bet_input {
  min-width: 150px;
  height: 30px;
}

.button {
  padding: 12px 50px;
  background-color: rgb(255, 186, 186);
  border-radius: 5px;
  color: red;
  font-size: 24px;
  border: none;
}
